import React from 'react'
import RedeemForm from '../models/redeem_form'
import Countries from '../models/countries'

const SIZING_PAGE_URL = "https://saltandsatoshi.com/#sizing"

type Props = {
  model: RedeemForm
  onSubmit: (model: RedeemForm) => any
  onClose: () => any
}
type State = {
  model: RedeemForm
}

class RedeemFormConponent extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props)
    this.state = {
      model: props.model,
    }
    this.onChange = this.onChange.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }

  render() {
    const { model } = this.props

    return (
      <section id="redemption3-section">
        <div id="container14" className="container default full screen">
          <div className="wrapper">
            <div className="inner">
              <h2 id="text30">Redeem NFT</h2>
              <p id="text28" className="style2">Fill in your sizing and delivery details</p>
              <form id="form02" onSubmit={this.onSubmit}>
                <div className="inner">
                  <div className="field">
                    <label htmlFor="form02-name">Email</label>
                    <input type="email" name="email" required
                      value={model.email}
                      onChange={event => {
                        model.email = event.currentTarget.value
                        this.onChange(model)
                      }}
                    />
                  </div>
                  <div className="field">
                    <label htmlFor="form02-name">Name</label>
                    <input type="text" name="name" id="form02-name" placeholder="" maxLength={256} required
                      value={model.name}
                      onChange={event => {
                        model.name = event.currentTarget.value
                        this.onChange(model)
                      }}
                    />
                  </div>
                  <div className="field">
                    <label htmlFor="form02-gender-for-sizing-and-measurements">Gender (for sizing and measurements)</label>
                    <select name="gender-for-sizing-and-measurements" id="form02-gender-for-sizing-and-measurements" required
                      value={model.gender}
                      onChange={event => {
                        model.gender = event.currentTarget.value
                        this.onChange(model)
                      }}
                    >
                      <option value="">&ndash;</option>
                      <option value="male">Male</option>
                      <option value="female">Female</option>
                      <option value="uknown">Prefer not to say (will receive male sizing)</option>
                    </select>
                  </div>
                  <div className="field">
                    <label style={{display: 'flex', justifyContent: 'space-between'}}>
                        Size
                        <sup><a href={SIZING_PAGE_URL} target="_blank">ü§î help me to choose</a></sup>
                    </label>
                    <select name="size" id="form02-size" required
                      value={model.size}
                      onChange={event => {
                        model.size = event.currentTarget.value
                        this.onChange(model)
                      }}
                    >
                      <option value="">&ndash;</option>
                      <option value="xs">Extra Small</option>
                      <option value="s">Small</option>
                      <option value="m">Medium</option>
                      <option value="l">Large</option>
                      <option value="xl">Extra Large</option>
                      <option value="xxl">Extra Extra Large</option>
                    </select>
                  </div>
                  <div className="field">
                    <label htmlFor="form02-address">Address</label>
                    <input type="text" name="address" id="form02-address" placeholder="" maxLength={256} required
                      value={model.address}
                      onChange={event => {
                        model.address = event.currentTarget.value
                        this.onChange(model)
                      }}
                    />
                  </div>
                  <div className="field">
                    <label htmlFor="form02-postcode">Postcode</label>
                    <input type="text" name="postcode" id="form02-postcode" placeholder="" maxLength={256} required
                      value={model.postcode}
                      onChange={event => {
                        model.postcode = event.currentTarget.value
                        this.onChange(model)
                      }}
                    />
                  </div>
                  <div className="field">
                    <label htmlFor="form02-country">Country</label>
                    <select name="country" id="form02-country" required
                      value={model.country}
                      onChange={event => {
                        model.country = event.currentTarget.value
                        this.onChange(model)
                      }}
                    >
                      <option value="">&ndash;</option>
                      {Countries.map(country => {
                        return (
                          <option value={country.code}>{country.name}</option>
                        )
                      }) }
                    </select>
                  </div>

                  <div className="field" style={{color: 'black', textAlign: 'justify'}}>
                    <p>Please be aware that all of our apparel products are handmade to size and need to be authenticated. Delivery of apparel may take between 4 and 8 weeks from the day of redemption.</p>
                    <br/>
                    <p>When you do get it - you're gonna be the coolest crypto cat in town.</p>
                  </div>

                  <div className="actions">
                    <button type="submit">Submit</button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    )
  }

  private onChange(model: RedeemForm) {
    const clone = Object.assign({}, model) as RedeemForm
    this.setState({
      model: clone
    })
  }

  private onSubmit(event: React.SyntheticEvent<HTMLFormElement>) {
    event.preventDefault()
    this.props.onSubmit(this.state.model)
    return false
  }
}

export default RedeemFormConponent