import * as Firebase from '../services/firebase'

export type RedeemForm = {
  owner: string
  tokenId: number

  name?: string
  gender?: string
  size?: string
  address?: string
  postcode?: string
  country?: string
  email?: string
}

const collection = Firebase.firestore.collection('redeem_forms')

function key(m: RedeemForm) {
  return `${m.owner}-${Date.now()}`
}

export async function index() {
  const snapshot = await collection.get()
  const tokens = snapshot.docs.map(doc => doc.data() as RedeemForm)
  return tokens
}

export async function indexByOwner(owner: string) {
  const snapshot = await collection.where('owner', '==', owner).get()
  const tokens = snapshot.docs.map(doc => doc.data() as RedeemForm)
  return tokens
}

export async function create(m: RedeemForm) {
  const k = key(m)
  const doc = collection.doc(k)
  await doc.set(m)
}

export async function getRedeemedTokenIds(owner: string) {
  const snapshot = await collection.where('owner', '==', owner).get()
  const models = snapshot.docs.map(doc => doc.data() as RedeemForm)
  return models.map(m => m.tokenId!)
}

export default RedeemForm