import * as Firebase from '../services/firebase'

export type Token = {
  contractAddress: string
  tokenId: number
  marketUrl: string
  imageUrl: string
  networkId: number
}

const collection = Firebase.firestore.collection('nft_tokens')

export async function indexByNetworkId(networkId: number) {
  const snapshot = await collection.where('networkId', '==', networkId).get()
  const tokens = snapshot.docs.map(doc => doc.data() as Token)
  return tokens
}